import Header from './components/header';
import MegaMenu from './components/megaMenu';
import Content from './components/content';
import Welcome from './components/welcome';

import { HashRouter as Router, Route, Switch } from 'react-router-dom';

function App() {
  return (
    <Router>
      <Header />
      <Switch>
        <Route path='/home' component={Content} />
        <Route path='/html' component={Content} />
        <Route path='/css' component={Content} />
        <Route path='/css-props' component={Content} />
        <Route path='/html-tags' component={Content} />
        <Route path='/excer-cises' component={Content} />
        <Route path='/typescript' component={Content} />
        <Route path='/javascript' component={Content} />
        <Route path='/git' component={Content} />
        <Route path='/react' component={Content} />
        <div className='three-columns'>
          <Route
            path='/js-methods'
            component={Content}
            tables='three-columns'
          />
        </div>
      </Switch>
      <MegaMenu />
    </Router>
  );
}
export default App;
